.list {
  display: block;
  list-style: none;
  background-color: whitesmoke;
  width: 365px;
  border-radius: 10px;
  box-shadow: 10px 5px 15px rgb(90, 87, 97);
  padding-top: 30px;
  padding-left: 0;
  padding-bottom: 30px;
  margin-bottom: 30px;
}

.item {
  display: flex;
  margin-left: 0;
  background-color: #fff;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 5px 3px 5px rgb(90, 87, 97);
}

.marker {
  display: flex;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background-color: red;
  margin: auto 30px;
}

.on {
  background-color: green;
}

.avatar {
  margin-right: 30px;
  width: 70px;
  background-color: rgb(176, 171, 171);
  border-radius: 5%;
  padding: 5px;
}

.name {
  font-size: 24px;
  font-weight: bold;
  color: rgb(67, 67, 70);
}